.container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.container img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-right: 20px;
}

.container div {
  text-align: left;
}

.container h1 {
  margin-bottom: 10px;
}

.container p {
  margin-bottom: 8px;
}

